import {  Avatar, Grid, LinearProgress, Typography } from '@material-ui/core'
import React , {useEffect} from 'react';
import "./SkillPage.css";
import AOS from 'aos';
import 'aos/dist/aos.css'; 

function SkillPage() {

    useEffect(() => {
        AOS.init({
            duration:1500,
            
        });
        
      }, []);



    return (
        <div className="skillPage">
            <Grid container direction="column"  style={{height:"100vh"}}>

            {/* skill */}
                <Grid  item className="skillPage__title" >
            <h3   data-aos="zoom-in"  style={{color:"white", marginRight:"40px", letterSpacing:"2px", fontFamily: "Poppins, sans-serif",marginTop:"90px",marginLeft:"40px",fontWeight:"400",marginBottom:"5px" ,borderRadius:"5px"}}>
              Skill
            </h3  >
               </Grid>

            {/* html/css */}
            <Grid  data-aos="fade-right" item className="skill__glassmorphisam" style={{margin:"5px 30px"}}>
            <p  style={{color:"black",margin:"5px 15px",fontFamily: "Poppins, sans-serif",letterSpacing:"1px",fontWeight:"600"}}>
               html/css
            </p >
            
            <LinearProgress variant="determinate" value={70} style={{ height:"10px",margin:"8px 10px",borderRadius:"20px"}}/>
            </Grid>

            {/* javaScript */}
            <Grid data-aos="fade-right" item className="skill__glassmorphisam" style={{margin:"5px 30px"}}>
            <p  style={{color:"black",margin:"5px 15px",fontFamily: "Poppins, sans-serif",letterSpacing:"1px",fontWeight:"600"}}>
                javaScript
            </p >
            
            <LinearProgress variant="determinate" value={70} style={{height:"10px",margin:"8px 10px",borderRadius:"20px"}} />
            </Grid>

            {/* react */}
            <Grid data-aos="fade-right"  item className="skill__glassmorphisam" style={{margin:"5px 30px"}}>
            <p  style={{color:"black",margin:"5px 15px",fontFamily: "Poppins, sans-serif",letterSpacing:"1px",fontWeight:"600"}}>
                React
            </p >
            
            <LinearProgress variant="determinate" value={60} style={{height:"10px",margin:"8px 10px",borderRadius:"20px"}} />
            </Grid>

             {/* pyhton */}
             <Grid data-aos="fade-right" item className="skill__glassmorphisam" style={{margin:"5px 30px"}}>
            <p  style={{color:"black",margin:"5px 15px",fontFamily: "Poppins, sans-serif",letterSpacing:"1px",fontWeight:"600"}}>
                Python
            </p >
            
            <LinearProgress variant="determinate" value={60} style={{height:"10px",margin:"8px 10px",borderRadius:"20px"}} />
            </Grid>

             {/* mysql */}
             <Grid data-aos="fade-right" item className="skill__glassmorphisam" style={{margin:"5px 30px"}}>
            <p  style={{color:"black",margin:"5px 15px",fontFamily: "Poppins, sans-serif",letterSpacing:"1px",fontWeight:"600"}}>
                MySQL
            </p >
            
            <LinearProgress variant="determinate" value={60} style={{height:"10px",margin:"8px 10px",borderRadius:"20px"}} />
            </Grid>

             {/* Ethical Hacker */}
             <Grid  data-aos="fade-right" item className="skill__glassmorphisam" style={{margin:"5px 30px"}}>
            <p  style={{color:"black",margin:"5px 15px",fontFamily: "Poppins, sans-serif",letterSpacing:"1px",fontWeight:"600"}}>
                Ethical Hacker
            </p >
            
            <LinearProgress variant="determinate" value={60} style={{height:"10px",margin:"8px 10px",borderRadius:"20px" }} />
            </Grid>

            </Grid>
        </div>
    )
}

export default SkillPage
