import {  Button, Divider, Grid, Typography } from '@material-ui/core'
import React,{ useEffect } from 'react';
import "./ContactPage.css";
import Title from 'react-vanilla-tilt'
import AOS from 'aos';
import 'aos/dist/aos.css'; 
function ContactPage() {

    useEffect(() => {
        AOS.init({duration:2000});
       
      }, []);



    return (
        <div className="contactPage">

          <Grid container  align="center" style={{color:"white",height:"100vh"}}>

                 {/* About left section */}
                 
                <Title  data-aos="zoom-in"  options={{ glare: "max-glare: 1,glare-prerender: false "}}  className="contactPage__glassMorphism"  style={{ color:"white", marginTop:"150px", marginBottom:"200px", marginLeft:"100px",marginRight:"100px",padding:"20px",width:"100%"}}>
                
                <Typography  style={{padding:"2px",color:"black",fontWeight:"600" ,letterSpacing:"3px" , fontFamily: "Poppins, serif"}} variant="h4" component="h4">
                    Contact
                </Typography>
                <Divider  />
                <br/>
                <h3 style={{ color:"black",fontFamily: "Poppins, serif" ,letterSpacing:"2px"}} >Email</h3>
                <br/>
                <p style={{fontFamily: "Poppins, serif"}} >raj267447@gmail.com</p>
                <br/>
                <h3 style={{ color:"black",fontFamily: "Poppins, serif" ,letterSpacing:"2px"}} >Mobile Number</h3>
                <br/>
                <a href="tel:+917717792439" download style={{color:"white",textDecoration:"none"}}><Button  variant="contained" color="primary" style={{fontFamily: "Poppins, sans-serif"}}>Call Now</Button></a>
                
                </Title >
                
             </Grid>

            
        </div>
    )
}

export default ContactPage
