import React,{ useEffect } from 'react';
import "./AboutPage.css";
import { Button, Divider, Grid, Typography } from '@material-ui/core';
import AOS from 'aos';
import 'aos/dist/aos.css'; 
import Title from 'react-vanilla-tilt'

function AboutPage() {

    useEffect(() => {
        AOS.init({duration:2000});
       
      }, []);

    return (
        <div className="aboutPage">
             <Grid container  align="center" style={{color:"white",height:"100vh"}}>

                 {/* About left section */}
                 
                <Title  options={{ glare: "max-glare: 1,glare-prerender: false "}} data-aos="zoom-in"  className="aboutPage__glassMorphism"  style={{ color:"white", marginTop:"150px", maxHeight:"350px", marginLeft:"100px",marginRight:"100px",padding:"20px",width:"100%"}}>
                
                <Typography  style={{padding:"2px",color:"black",fontWeight:"600" ,letterSpacing:"3px" , fontFamily: "Poppins, serif"}} variant="h4" component="h4">
                    About
                </Typography>
                <Divider  />
                
                <p style={{fontFamily: "Poppins, serif",letterSpacing:"2px",margin:"20px"}} >Web Developer || Python Programmer || Ethical Hacker</p>
                
                <p className="aboutPage__pData" style={{fontFamily: "Poppins, serif",letterSpacing:"2px",margin:"10px"}} >I am a proactive and enthusiastic learner with efficient management skills and leadership quality seeking a position where I can efficiently contribute my skills and creative abilities for the growth of the organization while building a professional career.</p>
                <br/>
                <h3 style={{ color:"black",fontFamily: "Poppins, serif" ,letterSpacing:"1px",marginBottom:"10px"}} >Download My Resume</h3>
                
                <a href="Raj_Aryan_resume.pdf" download style={{color:"white",textDecoration:"none"}}><Button  variant="contained" color="primary" style={{fontFamily: "Poppins, sans-serif"}}>Click Here</Button></a>
                
                </Title >
                
             </Grid>
        </div>
    )
}

export default AboutPage
