import React,{useEffect} from 'react'
import "./Project.css"
import ProjectPage from './Component/ProjectPage'
import {  Grid } from '@material-ui/core'
import AOS from 'aos';
import 'aos/dist/aos.css'; 

function Project() {
    useEffect(() => {
        AOS.init({
            duration:1500,
            
        });
        
      }, []);
    return (
        <div className="project">
             <Grid contanier >
            <h3 data-aos="zoom-in" className="project__title"   style={{color:"white", marginRight:"40px", letterSpacing:"2px", fontFamily: "Poppins, sans-serif",paddingTop:"90px",marginLeft:"50px",fontWeight:"400",marginBottom:"5px" ,borderRadius:"5px"}}>
              Project
            </h3  >
            </Grid>
               
            <Grid  container  style={{}}  justify="center">
            
             <Grid item  style={{marginTop:"10px",padding:"10px"}}  >
             <ProjectPage 
             image="https://miro.medium.com/max/3840/1*yjH3SiDaVWtpBX0g_2q68g.png" 
             title="React"
             button="" 
             />
             </Grid>
            
             <Grid className="project__card2" item style={{marginTop:"10px",padding:"10px"}} >
             
             <ProjectPage 
             image="https://www.brainstormindia.com/images/resources/web.jpg" 
             title="Html || CSS || JavaScript" 
             />

             </Grid>
            
             <Grid className="project__card3" item style={{marginTop:"10px",padding:"10px"}} >
             <ProjectPage 
             image="https://cdn-images-1.medium.com/max/2560/1*O7ttrU2PqHXwPYni8RSA4w.png" 
             title="ThreeJs" 
             />
             </Grid>

             <Grid className="project__card4" item style={{marginTop:"10px",padding:"10px"}} >
             <ProjectPage 
             image="https://ponirevo.com/wp-content/uploads/2020/04/python-language-why-one-should-learn-it-and-how-it-can-help.jpg" 
             title="Python" 
             />
             </Grid>

            </Grid>
        </div>
    )
}

export default Project
