import { Button,   CardActionArea,  CardActions,  CardContent,  CardMedia,  Grid,  Typography,} from "@material-ui/core";
import React from "react";
import "./ProjectPage.css";
import { makeStyles } from '@material-ui/core/styles';
import Title from 'react-vanilla-tilt'
const useStyles = makeStyles({
    root: {
      maxWidth: 300,
    },
  });

function ProjectPage({image,title}) {
    const classes = useStyles();
  return (
    <div className="projectPage" >
        {/* card 1 */}
       
            
          
      <Title className={classes.root} style={{ borderRadius:"5px", background: "rgba(255,255,255, 0.2)", padding:"0px",backdropFilter: "blur(10px)"}}  >
        <CardActionArea>
          <CardMedia
             component="img"
             alt="Contemplative Reptile"
             height="150"
            image={image}
            title={title}
            style={{borderRadius:"5px 5px 0px 0px"}}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              {title}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              Lizards are a widespread group of squamate reptiles, with over
              6,000 species, ranging across all continents except Antarctica
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{justifyContent:"space-between"}}>
          <Button size="small" color="primary">
            Share
          </Button>
          <Button size="small" color="primary">
            Learn More
          </Button>
        </CardActions>
      </Title>
      
    </div>
  );
}

export default ProjectPage;
