import { Grid, Typography } from '@material-ui/core'
import FacebookIcon from '@material-ui/icons/Facebook';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import TwitterIcon from '@material-ui/icons/Twitter';
import React from 'react'
import "./Footer.css"

function Footer() {
    return (
        <div className="footer">
            <Grid container 
                   justify="space-between"
                   alignItems="center"
                   style={{bottom:0,
                   width:"100%",
                   padding:"20px",
                   background: "rgba(21, 25, 33, 0.46)"}}
                   
                   >
                <Grid item style={{color:"white"}}>
                <Typography  variant="subtitle2" component="subtitle2" style={{fontFamily: "Poppins, sans-serif",letterSpacing:"2px"}}>
                  Copyright &copy; 2020 
                </Typography>
                </Grid>

                <Grid  item  spacing={5} direction="column" style={{color:"white",}}>
                <FacebookIcon style={{margin:"0 10px"}} /> 
                <WhatsAppIcon style={{margin:"0 10px"}} />
                <TwitterIcon  style={{margin:"0 10px"}} />
                </Grid>
            </Grid>

        </div>
    )
}

export default Footer
